import Vue from 'vue'

import $ from "jquery";
import UTILS from './Utils'
import GLOBAL from './Global'

let instance = {
    init() {
        Vue.mixin({
            data() {
                return {
                    get GLOBAL() {
                        return GLOBAL;
                    },
                
                    UTILS,
                    JQ: $,
                }
            }
        })
    }
}

export default instance