<template>
    <div class = 'filter-container'>
        <div class = 'menu-container'>
            <p class = 'menu' :class = '{activeMenu: GLOBAL.filterType == menu.type}' v-for = '(menu, index) in menus' :key = 'index' @click = 'changeMenu(menu)'>{{menu.name}}</p>
        </div>

        <div class = 'search-container'>
            <label for="search">
                <img src="../assets/icons/search.svg" alt="">
            </label>
            <input id = 'search' type="text" class = 'inputSearch' placeholder="PESQUISAR VEÍCULO" v-model = 'GLOBAL.filterSearch'>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            menus: [
                {name: 'CARROS', type: 'Carros'}, 
                {name: 'MOTOS', type: 'Motos'},
                {name: 'POSSUÍDOS', type: 'Possuidos'},
            ]
        }
    },

    methods: {
        changeMenu(menu) {
            this.GLOBAL.filterType = menu.type
            this.$bus.emit('changeType-event')
        }
    }
}
</script>

<style scoped>
.filter-container {
    background: #111111;
    height: 60px;
    width: 100%;
    border-radius: 6px;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/* ================================================================ */

.menu-container {
    display: flex;
    align-items: center;
}

.menu-container .menu {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 11px;
    text-transform: uppercase;

    color: rgba(255, 255, 255, 0.2);
    margin-right: 20px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: all .5s;
    cursor: pointer;
}

.activeMenu {
    color: white !important;

}

.activeMenu::before {
    content: '';
    display: block;
    position: absolute;
    top: -25px;
    height: 2px;
    width: 60%;
    background: #848CEB;
    border-radius: 100px;
    animation-name: fade;
    animation-duration: .5s;
}

/* ================================================================ */

.inputSearch {
    height: 35px;
    width: 200px;
    border-radius: 5px;
    padding: 10px;

    font-family: 'Gotham';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    
    background-color: rgba(0, 0, 0, 0.018);

    outline: transparent;
    border: none;

    color: white;
}

.inputSearch::placeholder {
    color: rgba(255, 255, 255, 0.2);
}

/* =========================================================== */

.search-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.481);
    border-radius: 5px;
    padding: 0px 10px;

}
.search-container img {
    height: 15px;
    opacity: .6;
    margin-right: 0px;
}

</style>