<template>
    <div class = 'apresentation-container fade' v-if = 'GLOBAL.selectedVehicle.price'>
        <h1 class = 'name'>{{GLOBAL.selectedVehicle.name}}</h1>

        <div class = 'infos-container' :class = 'GLOBAL.filterType == "Possuidos" ? "doubleGrid" : "tripleGrid"'>
            <div class = 'info fade'>
                <img :src="require('../assets/icons/money.svg')" >
                <h1>{{UTILS.formatNumberToMoney(GLOBAL.selectedVehicle.price)}}</h1>
                <p>VALOR</p>
            </div>
            <div class = 'info fade'>
                <img :src="require('../assets/icons/tax.svg')" >
                <h1>{{GLOBAL.filterType == 'Possuidos' ? GLOBAL.selectedVehicle.tax : UTILS.formatNumberToMoney(GLOBAL.selectedVehicle.tax)}}</h1>
                <p>TAXA SEMANAL</p>
            </div>
            <div class = 'info fade' v-if = 'GLOBAL.filterType != "Possuidos"'>
                <img :src="require('../assets/icons/trunk.svg')" >
                <h1>{{GLOBAL.selectedVehicle.chest}}KG</h1>
                <p>PORTA-MALAS</p>
            </div>
        </div>

        <div class = 'buttons-container'>
            <div class = 'button' @click = 'makeAction("requestDrive")' v-if = 'GLOBAL.filterType != "Possuidos"'>
                <h1>TESTAR</h1>
                <p>TEST-DRIVE</p>
            </div>
            <div class = 'button' @click = 'makeAction("requestBuy")' v-if = 'GLOBAL.filterType != "Possuidos"'>
                <h1>COMPRAR</h1>
                <p>ADQUIRIR</p>
            </div>
            
            <div class = 'button' @click = 'makeAction("requestSell")' v-if = 'GLOBAL.filterType == "Possuidos"'>
                <h1>VENDER</h1>
                <p>VENDER</p>
            </div>
            <div class = 'button' @click = 'makeAction("requestTax")' v-if = 'GLOBAL.filterType == "Possuidos"'>
                <h1>PAGAR</h1>
                <p>PAGAR TAXA</p>
            </div>
        </div>

        <div class = 'infos2-container'>
            <h1>INFORMAÇÕES</h1>
            <p>Os melhores veículos da cidade.</p>
        </div>
    </div>
</template>

<script>
export default {
    methods: {
        makeAction(action) {
            let options = {
                method: 'POST',
                body: JSON.stringify({name: this.GLOBAL.selectedVehicle.k})
            }

            fetch(this.GLOBAL.url + '/' + action, options)
        }
    }
}
</script>

<style scoped>
.apresentation-container {
    height: 100%;
    width: 100%;

    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 3;
    padding: 10px;

    display: flex;
    flex-direction: column;
}

.name {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 19px;

    color: #FFFFFF;
    margin-top: 10px;
}

/* ================================================= */

.infos-container {
    width: 100%;
    height: 95px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 40px;
    grid-gap: 20px;
    justify-content: space-between  ;
}

.doubleGrid {
    grid-template-columns: repeat(2, 1fr);
}

.tripleGrid {
    grid-template-columns: repeat(3, 1fr);
}

/* ================================================= */

.info {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.info img {
    height: 40px;
}

.info h1 {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 10px;
    color: white;
    margin-top: 15px;
}

.info p {
    margin-top: 5px;
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 600;
    font-size: 11px;

    color: rgba(255, 255, 255, 0.2);

}

/* ================================================================== */

.buttons-container {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    margin-top: 20px;
}

.button {
    width: 100%;
    height: 100%;   
    background: rgba(17, 17, 17, 0.6);
    border-radius: 6px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: all .5s;
    cursor: pointer;
}

.button:hover {
    background: rgb(17, 17, 17);
}

.button::before {
    content: '';
    background: #848CEB;
    display: block;
    position: absolute;
    left: 0px;
    width: 2.5px;
    height: 60%;
    border-radius: 1000px;

    top: 50%;
    transform: translateY(-50%);
}

.button h1 {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    color: white;
}


.button p {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 400;
    font-size: 11px;

    color: rgba(255, 255, 255, 0.2);
}

/* =========================================================== */

.infos2-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 20px;

}

.infos2-container h1 {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;

    color: #848CEB;
}

.infos2-container p {
    font-family: 'Gotham';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;

    color: rgba(255, 255, 255, 0.2);
    margin-top: 10px;
    line-height: 14px;
    width: 60%;
    text-align: center;
}

</style>