<template>
	<div id="app" style = 'display: none'>
        <div class = 'content'>
            <FilterMenu />

            <div class = 'vehicles-container'>
                <Vehicle v-for = '(vehicle, index) in vehicles' :key = 'index' :style = '{display: vehicleAppear(vehicle) ? "flex" : "none"}' :data = 'vehicle' @click.native = 'selectVehicle(vehicle)'/>
            </div>

            <Apresentation />
        </div>
	</div>
</template>

<script>
import Apresentation from './components/Apresentation.vue'
import Vehicle from './components/Vehicle.vue'
import FilterMenu from './components/FilterMenu.vue'

export default {
    components: { 
        FilterMenu, Vehicle, Apresentation 
    },

    data() {
        return {
            vehicles: [
                {k: 'kuruma_armored1', name: 'KURUMA BLINDADO', tax: 31, chest: 50,  price: 910821},
            ]
        }
    },

    methods: {
        getVehicles() {
            let options = {
                method: 'POST',
                body: JSON.stringify({})
            }

            fetch(this.GLOBAL.url + '/request' + this.GLOBAL.filterType , options)
            .then(async response => await response.json())
            .then(async response => {
                this.vehicles = response.result

                this.GLOBAL.selectedVehicle = this.vehicles[0]

                if (this.vehicles.length <= 0) {
                    this.GLOBAL.selectedVehicle = {name: '---'}
                }
            })
        },

        selectVehicle(vehicle) {
            this.GLOBAL.selectedVehicle = vehicle
        }, 

        vehicleAppear(vehicle) {
            if (vehicle.name.toLowerCase().indexOf(this.GLOBAL.filterSearch.toLowerCase()) >= 0) {
                return true
            } else {
                return false
            }
        }
    },

    mounted() {
		this.listener = window.addEventListener('message', ({data}) => {
            if (data.action == 'openSystem') {
                this.JQ('#app').fadeIn(500)
                this.getVehicles()
            } else if (data.action == 'closeSystem') {
                this.JQ('#app').fadeOut(500)
            }
        }, false);

        this.$bus.on('changeType-event', () => {
            this.getVehicles()
        }) 

        this.listener = window.addEventListener('keyup', ({keyCode}) => {
            if (keyCode == '27') {
                this.JQ('#app').fadeOut(500)

                let options = {
                    method: 'POST',
                    body: JSON.stringify({})
                }

                fetch(this.GLOBAL.url + '/closeSystem', options)
            } 
        }, false);
	}
}
</script>

<style scoped>
#app {
    height: 100%;
    width: 100%;

    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
}

/* ==================================================================================== */

.content {
    background: rgba(17, 17, 17, 0.7);
    border: 2px solid rgba(255, 255, 255, 0.09);
    border-radius: 6px;
    width: 1160px;
    height: 605px;
    padding: 20px;

    display: grid;
    grid-template-columns: 700px 1fr;
    grid-template-rows: 60px 1fr;
    grid-gap: 20px;
}

/* ============================================================= */

.vehicles-container {
    height: 100%;
    width: 100%;
    grid-column-start: 1;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: repeat(auto-fill, 60px);
    grid-gap: 10px;
    overflow-y: scroll;
}



</style>

